import popMe from "@/components/popupMe.vue";
export default {
	components:{
		popMe
	},
	data() {
		return {
			secondPassword:'',
			userDetail:'',
			summary:''
		};
	},
	onNavigationBarButtonTap:function(val){
		 
		uni.navigateTo({
			url: '/pages/capital/money',
		
		}); 
	    
	},
	onLoad:function(){
		this.fetch()
		this.fetchSumary()
	},
	methods:{
		
		async fetch(){
			const res  = await this.$http.get('/user/details')
			console.log(res)
			this.userDetail = res.data.data
			
			
		},
		async fetchSumary(){
			const res  = await this.$http.get('/user/summarizing')
			console.log(res)
			this.summary = res.data.data

		},
		toSubAccounter:function(){
			uni.navigateTo({
				url: './subAccounter'
			});
		},
		toManage:function(){
			this.$refs.dialog.show()
		},
		confirmInput:function(e){
			this.secondPassword = e
		},
		toSetting:function(){
			uni.navigateTo({
				url: './setting',
			});
		},
		toTeamNode:function(){
			if (this.userDetail.staticUserStatus <1){
				uni.showToast({
					title: '请先激活认购',
					icon: 'none',
					duration: 2000
				});
				return
			}
			uni.navigateTo({
				url: '/pages/team/team',
				
			});
		}
	}
}
